import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import DemoCn from '../views/demo/Demo-Cn'
import DemoEn from '../views/demo/Demo-En'
import DemoKeynote from '../views/demo/Demo-Keynote'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        redirect: '/demo-cn'
    },
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/test1',
        name: 'test1',
        component: () => import('../views/test/Test1.vue')
    }, {
        path: '/test2',
        name: 'test2',
        component: () => import('../views/test/Test2.vue')
    }, {
        path: '/test3',
        name: 'test3',
        component: () => import('../views/test/Test3.vue')
    }, {
        path: '/test4',
        name: 'test4',
        component: () => import('../views/test/Test4.vue')
    }, {
        path: '/demo-en',
        name: 'demo-en',
        component: DemoEn
    }, {
        path: '/demo-cn',
        name: 'demo-cn',
        component: DemoCn
    }, {
        path: '/demo-keynote',
        name: 'demo-keynote',
        component: DemoKeynote
    }
]

const router = new VueRouter({
  routes
})

export default router
